import React, { PureComponent } from "react";
import { connect } from 'react-redux'; 
import { isTagShop } from "../../../../actions/api";
import { nextData } from "../../../../actions/moredataAction";
import "./scss/SeeMore.scss";

class SeeMore extends PureComponent {
  onLoadeMore = event => {
    const { postData, ThemeStyle, ThemeInfo } = this.props;
    const { appendData } = postData;
    const { webId, wallId, id } = ThemeInfo;
    const postCountTagshop = ThemeStyle.column.desktop < 5 ? 5 : ThemeStyle.column.desktop
    const postCount = isTagShop && id == 53 ? postCountTagshop : ThemeStyle.totalPosts;
    const timeStamp = Math.floor(Date.now() / 1000);
    this.props.nextData(isTagShop ? webId : wallId, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent);
  }
  render() {

    const { loaderData, postData, ThemeInfo, ThemeStyle } = this.props;
    const  isBranding = ThemeInfo.branding && isTagShop ? true : false
    const hasMoreData = postData.hasMoreData && Object.keys(postData.hasMoreData).length ? postData.hasMoreData[0] && postData.hasMoreData[0].hasMoreData == false ? false : postData.hasMoreData[postData.appendData.networkID] ? postData.hasMoreData[postData.appendData.networkID].hasMoreData : true : true
    const { showMore, noMore } = ThemeStyle.language.text
    return (
      hasMoreData === true ?
        <div className="tb_see_more_btn_wrap" style={{ paddingTop: isBranding ? 5 : '' }}>
          <div className={`tb_see_more_btn ${(loaderData && loaderData.isShowMoreLoading) ? `tb_more_btn_loading` : ``}`} onClick={this.onLoadeMore} role="button" tabIndex="0">{hasMoreData ? showMore : noMore}</div>
        </div>
        : <></>
    );
  }
}


const mapStateToProps = state => {
  return {
    loaderData: state.loaderData,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    nextData: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(nextData(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SeeMore);